<script>import { onMount, getContext, hasContext, onDestroy, } from "svelte";
import { actionWatchSize } from "../../../actions/watchSize/watchSize";
/** choose default theme colors */
export let textareaStyle = "dark";
/** pass the function to validation */
export let validationFn = () => true;
/** if you want to force invalid, change it to true */
export let forceInvalid = false;
/** shows if the component is valid (readonly) */
export let isValid = true;
/** Enter label text */
export let label = "";
/** number of initial lines */
export let rows = 2;
/** maximum number of lines before scroll */
export let maxRows = 0;
/** set an error message */
export let errorMsg = "";
/** write a helpertext if needed */
export let helperText = "";
/** Enable the textarea to be resizable */
export let resizable = false;
/**
 * The textarea element (readonly)
 * @type {HTMLTextAreaElement}
 * */
export let textareaElement = null;
/**
 * Required
 * @type {string}
 */
export let name;
export let disabled = false;
export let value = "";
export let required = false;
export let readonly = false;
export let placeholder = "";
export let id = name;
// Other attributes for the HTML textarea element
export let textareaAttributes = {};
let eMsg = "deu ruim";
let helper = false;
let invalid = forceInvalid;
let wrapperElement;
let clipPathVariables = {
    borderWidth: "0px",
    labelWidth: "0px",
    labelHeight: "0px",
};
let labelComponent;
const isInsideContext = hasContext("FormContext");
const { setFieldValue, addFieldToContext, removeFieldFromContext, } = isInsideContext && getContext("FormContext");
const focused = () => {
    if (invalid) {
        helper = false;
    }
    else {
        helper = !helper;
    }
};
const checkStatus = (answer) => {
    if (answer === true || answer === undefined) {
        isValid = true;
        invalid = !isValid;
    }
    else if (answer === false) {
        isValid = false;
        invalid = !isValid;
        eMsg = errorMsg;
    }
    else if (typeof answer === "string") {
        isValid = false;
        invalid = !isValid;
        eMsg = answer;
    }
};
const changed = () => {
    invalid = false;
    helper = true;
};
const validation = () => {
    if (forceInvalid) {
        isValid = false;
        invalid = !isValid;
        eMsg = errorMsg;
    }
    else if (required && !value) {
        isValid = false;
        invalid = !isValid;
        eMsg = "Este campo é obrigatorio";
    }
    else {
        checkStatus(validationFn(value));
    }
};
const setValue = (ev) => {
    const x = ev.target.value;
    value = x;
};
// function to form context force input value
const forceValue = (_value) => {
    value = _value;
};
function handleLabelResize(ev) {
    clipPathVariables = Object.assign(Object.assign({}, clipPathVariables), { labelHeight: `${ev.detail.blockSize}px`, labelWidth: `${ev.detail.inlineSize}px` });
}
$: if (forceInvalid)
    validation();
$: minHeight = `${1 + rows * 1.2}em`;
$: maxHeight = maxRows ? `${1 + maxRows * 0.8}rem` : "auto";
// run only after mounted, because setFieldValue, must become after addFieldToContext
$: if (textareaElement && isInsideContext) {
    setFieldValue(name, value, isValid);
}
$: if (wrapperElement && labelComponent) {
    clipPathVariables = Object.assign(Object.assign({}, clipPathVariables), { borderWidth: `${getComputedStyle(wrapperElement).borderWidth}` });
}
onMount(() => {
    if (isInsideContext) {
        addFieldToContext(name, value, isValid, required, wrapperElement, validation, forceValue);
    }
});
onDestroy(() => {
    if (isInsideContext) {
        removeFieldFromContext(name);
    }
});
</script>

<div
  class="content-container"
  style="
    --border-width: {clipPathVariables.borderWidth};
    --label-height: {clipPathVariables.labelHeight};
    --label-width: {clipPathVariables.labelWidth};
  "
>
  <div
    class="textarea-container textarea-style-{textareaStyle}"
    class:invalid
    bind:this={wrapperElement}
  >
    <div
      class="textarea-wrapper"
      class:resizable
      style="--max-auto-height:{maxHeight}"
    >
      <pre style="min-height:{minHeight}; max-height:{maxHeight}"
        aria-hidden="true"
      >
        {`${value || placeholder}\n`}
      </pre>
      <textarea
        on:focus={focused}
        on:input={changed}
        on:input={setValue}
        on:blur={focused}
        on:blur={validation}
        on:input
        on:change
        bind:this={textareaElement}
        {rows}
        {name}
        {id}
        {placeholder}
        {value}
        {disabled}
        {readonly}
        {required}
        {...textareaAttributes}
      />
    </div>
    <label
      for="{id}"
      class="label"
      class:required
      bind:this={labelComponent}
      use:actionWatchSize
      on:actionResize={handleLabelResize}
    >
      <div class="label-text">
        {label}
      </div>
    </label>

  </div>
  <p class="helper message" class:helper-show={helper}>{helperText}</p>
  <p
    class="error message"
    class:error-show={invalid}
    class:show={(helper && helperText?.length > 0) || invalid}
  >
    {eMsg}
  </p>
</div>

<style lang="scss">/*
 You can use this to set border with gradient or solid colors, like:
    @include m.border(solid, 2px, linear-gradient(-30deg, rgba(252,0,47,0.938813025210084) 0%, rgba(170,255,6,1) 100%));
    @include m.border(solid, 2px, red);

 NOTE: to overwrite the border you must do this on the before element.
 NOTE2: This don't accept border style
*/
