  --top-left-corner-component: 0 0;
  --top-right-corner-component: 100% 0%;
  --bottom-left-corner-component: 0% 100%;
  --bottom-right-corner-component: 100% 100%;
  --horizontal-dislocation: calc(12.8px + 0px + var(--border-width));
  --left-side: calc(var(--horizontal-dislocation) - 0px);
  --right-side: calc(var(--left-side) + var(--label-width) + 0px);
  --top-side: 0;
  --bottom-side: calc((var(--label-height) / 2) + var(--border-width));
  --top-left-corner-label: var(--left-side) var(--top-side);
  --top-right-corner-label: var(--right-side) var(--top-side);
  --bottom-left-corner-label: var(--left-side) var(--bottom-side);
  --bottom-right-corner-label: var(--right-side) var(--bottom-side);
  clip-path: polygon(var(--top-left-corner-component), var(--top-left-corner-label), var(--bottom-left-corner-label), var(--bottom-right-corner-label), var(--top-right-corner-label), var(--top-right-corner-component), var(--bottom-right-corner-component), var(--bottom-left-corner-component));
}

