  content: "";
  z-index: -1;
  box-sizing: content-box;
  position: absolute;
  inset: 50% 0 0 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
  border-radius: inherit;
  border: inherit;
  background: var(--border-color);
  background-origin: border-box;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) content, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.label {
  position: absolute;
  top: -0.5rem;
  left: 0.8rem;
  padding: var(--label-padding);
  background: var(--label-background-color);
  font-size: calc(0.7 * var(--theme-fields-font-size));
}
.label .label-text {
  color: transparent;
  background: var(--label-color);
  background-clip: text;
  -webkit-background-clip: text;
}
.label.required .label-text::after {
  content: "*";
