  /* Firefox 19+ */
  color: transparent;
  background: var(--placeholder-color);
  background-clip: text;
  -webkit-background-clip: text;
}
textarea:-ms-input-placeholder {
  color: transparent;
  background: var(--placeholder-color);
  background-clip: text;
  -webkit-background-clip: text;
}
